import * as yup from 'yup';

export const extractErrors = error => {
  return error.inner.reduce((acc, error) => {
    return {...acc, [error.path]: error.message}
  }, {});
}

export const contentValidate = yup.object().shape({
  formContent: yup.string().required('  .').label(''),
});

export const loginValidate = yup.object().shape({
  formEmail: yup.string().required(' .').email('    .').label(''),
  formPassword: yup.string().required(' .').label(''),
});

export const registerValidate = yup.object().shape({
  formEmail: yup.string().required('  .').email('  . '),
  formPassword: yup.string().required('  .'),
  formPasswordConfirm: yup.string().required(' .').oneOf([yup.ref('formPassword'), null ],'    .').label(' '),
});
